.people {
  box-sizing: border-box;
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 200px);
  justify-content: center;
  align-items: center;
  gap: 44px 30px;
  font-family: Arial, Helvetica, sans-serif;
  transition: transform 1s ease-in-out 1s;
  display: grid;
  overflow-x: hidden;
  transform: scale(1);
}

@media (width >= 500px) {
  .people {
    --columns: 2;
  }
}

@media (width >= 750px) {
  .people {
    --columns: 2;
  }
}

@media (width >= 1000px) {
  .people {
    --columns: 4;
  }
}

@media (width >= 1500px) {
  .people {
    --columns: 4;
  }
}

.people__person {
  z-index: 0;
  width: 200px;
  height: 231px;
  position: relative;
}

.people__person:hover {
  z-index: 1;
  background-color: #eee;
  border-radius: 10px;
  transition: transform 1s ease-in-out;
  transform: scale(1.2);
}

.people__photo {
  align-items: center;
  width: 200px;
  height: 200px;
  display: flex;
  overflow: hidden;
}

.people__image {
  object-fit: cover;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  transition: border-radius 1s;
}

.people__rank {
  color: #8b0000;
  font-style: 16px;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-weight: bold;
  line-height: 18.4px;
  display: flex;
  position: absolute;
  top: 84px;
  right: 10px;
}

.people__details {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.people__name {
  color: #2f4f4f;
  font-size: 20px;
}

.people__extra {
  opacity: 0;
  text-align: center;
  width: 100%;
  font-size: 18px;
  transition: opacity 1s;
  position: absolute;
  top: 100%;
}

.people__person:hover .people__image {
  border-radius: 12%;
}

.people__person:hover .people__extra {
  opacity: 1;
}

.people__person:not(:hover) {
  transition: transform 1s ease-in-out;
  transform: scale(1);
}

.people__person:not(:hover) .people__extra {
  opacity: 0;
}
